<script>
export default {
  data() {
    return {
      //
      order: [],
      //
      expressnum: "",
      //
      nameOrTel: "",
      //
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      selecTime: "",
      //
      Stutus: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      orderStutus: "",

      //
      type: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      ordertype: "",

      //
      form: [
        {
          value: "APP",
          label: "APP"
        },
        {
          value: "PC",
          label: "PC"
        }
      ],
      orderform: "",

      //
      handler: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],

      //
      orderhandler: "",

      indexhandler: '',

      //
      strip: 0,

      //
      totallPages: 0,

      //
      currentPage4: 1,

      //
      page_size: 5,

      //index
      index: 1
    };
  },

  created() {
    this.getOrder();
  },

  filters: {
    //
    filterTime(time) {
      const time1 = new Date(time);
      const year = time1.getFullYear();
      const month = time1.getMonth() + 1;
      const data = time1.getDate();
      const hours = time1.getHours();
      const Minutes = time1.getMinutes();
      const Seconds = time1.getSeconds();
      return `${year}-${month}-${data}  ${hours}:${Minutes}:${Seconds}`;
    }
  },

  methods: {
    async getOrder(url) {
      //ajax
      const order = await this.$axios(
        "http://localhost:3000/getorder?index=" +
          this.index +
          "&conust=" +
          this.page_size
      );
      // console.log(order.data);
      this.order = order.data.limitData;
      console.log(order);
      //
      this.strip = order.data.strip;
      //
      this.totallPages = order.data.totalPage;
    },

    onhandler() {
      if (this.orderhandler) {
        if (this.indexhandler) {
          // this.$axios()
        } else {
          this.$message.error("!!");
        }
      } else {
        this.$message.error("!!");
      }
    },

    handleSelectionChange(selectArr) {
      console.log("handleSelectionChange", selectArr);
      if (Boolean(selectArr.join(""))) {
        selectArr.forEach(element => {
          this.indexhandler.push(element._id);
        });
      }
    },

    toOrderDetails(id) {
      this.$router.push({
        name: "OD",
        params: { id }
      });
    },

    //
    toShipment(id) {
      this.$router.push({
        name: "OFS",
        params: { id }
      });
    },

    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.page_size = val;
      this.getOrder();
    },

    handleCurrentChange(val) {
      // console.log(` ${val} `);
      this.index = val;
      this.getOrder();
    }
  }
};
</script>
